.sign-up-button {
  background-color: black;
  color: white;
  min-width: 165px;
  width: auto;
  height: 50px;
  letter-spacing: 0.5px;
  line-height: 50px;
  padding: 0 35px 0 35px;
  font-size: 15px;
  text-transform: uppercase;
  font-family: 'Open Sans Condensed';
  font-weight: bolder;
  cursor: pointer;


  &:hover {
    background-color: white;
    color: black;
    border: 1px solid black;
  }
  }
  